body {
  padding-top: 50px;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 14px;
  line-height: 1.15;
  color: #252626; }

img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto; }

.container {
  padding: 0 16px;
  margin: 0 auto;
  max-width: 1172px; }

.afisha {
  display: grid;
  grid-template-columns: repeat(auto-fit, 31.9%);
  grid-gap: 5px 2.1%; }
  .afisha__item {
    position: relative;
    display: flex;
    flex-direction: column; }
    .afisha__item_special {
      grid-row: 1/ span 2; }
      .afisha__item_special .special-tool {
        position: absolute;
        top: 11px;
        right: 16px;
        padding: 4px 0;
        width: 122px;
        display: block;
        font-size: 12px;
        line-height: 12px;
        font-weight: 600;
        letter-spacing: 0.02em;
        color: #fff;
        text-transform: uppercase;
        text-align: center;
        background-color: #3E99ED;
        border-radius: 6px 6px 0px 6px;
        z-index: 2; }
      @media (max-width: 736px) {
        .afisha__item_special .title-link img {
          margin: auto;
          width: 37%; } }
    @media (max-width: 736px) {
      .afisha__item {
        margin-bottom: 8px; } }
  .afisha .user {
    padding-left: 10px;
    display: flex;
    align-items: center; }
    .afisha .user__img {
      margin-right: 8px;
      width: 28px;
      height: 28px;
      border-radius: 50%;
      border: 1px solid #ccc;
      overflow: hidden; }
      .afisha .user__img img {
        display: block;
        max-width: 100%; }
    .afisha .user__name {
      font-weight: 600; }
  .afisha__picture {
    margin-bottom: 10px;
    position: relative;
    border-radius: 14px 0px 14px 14px;
    overflow: hidden;
    background-color: #EAE9D6; }
    .afisha__picture .title-link {
      display: block;
      font-size: 32px;
      line-height: 32px;
      color: #fff;
      text-decoration: none; }
      .afisha__picture .title-link:hover img {
        transform: scale(1.1); }
      @media (max-width: 736px) {
        .afisha__picture .title-link {
          font-size: 26px;
          line-height: 28px; } }
    .afisha__picture img {
      transition: transform 0.3s ease-in; }
    .afisha__picture .date {
      position: absolute;
      top: 16px;
      right: 16px;
      min-width: 73px;
      font-weight: 600;
      color: #fff;
      text-align: center;
      background-color: rgba(62, 63, 64, 0.7);
      border-radius: 8px 0px 8px 8px;
      z-index: 2; }
      .afisha__picture .date__num {
        margin: 0;
        font-size: 32px;
        line-height: 1.25; }
      .afisha__picture .date__month {
        margin: 0; }
      .afisha__picture .date__time {
        margin: 0 0 8px 0; }
    .afisha__picture_no-img {
      padding-left: 7%;
      padding-top: 8%;
      height: 100%;
      max-height: 224px;
      background-color: #CCC8A2; }
      .afisha__picture_no-img .title-link {
        max-width: 230px; }
      @media (max-width: 736px) {
        .afisha__picture_no-img {
          padding-top: 5%;
          padding-bottom: 5%; } }
  .afisha__location {
    padding: 0 10px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .afisha__location .city {
      font-weight: 600;
      color: #3E99ED; }
    .afisha__location .online {
      color: #48B0A2; }
  .afisha__title {
    margin: 0; }
    .afisha__title a {
      padding-left: 10px;
      display: block;
      font-size: 20px;
      line-height: 24px;
      color: #252626;
      text-decoration: none; }
  .afisha__more {
    display: block;
    padding: 8px 0;
    margin: 40px auto 0 auto;
    min-width: 364px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 600;
    text-align: center;
    color: #fff;
    border: none;
    border-radius: 24px;
    background-color: #3E99ED;
    cursor: pointer; }
  @media (max-width: 1023px) {
    .afisha {
      grid-template-columns: repeat(auto-fit, 48.9%); } }
  @media (max-width: 736px) {
    .afisha {
      grid-template-columns: 1fr; } }
